import { db } from "../firebase";
import { collection, addDoc } from "firebase/firestore";

/**
 * Creates a new item in the "items" collection in Firestore.
 * @param {Object} itemData - The item details from user input.
 * @returns {Promise<string>} The document ID of the newly created item.
 */
export const createItem = async (itemData) => {
    try {
        const now = new Date();
        const formattedTime = now.toISOString().replace(/[-T:.Z]/g, "").slice(0, 14); // YYYYMMDDHHMMSS

        const newItem = {
            category: itemData.category || "UNCATEGORIZED",
            cleanupNumber: 0,
            lastCleanUp: (parseInt(formattedTime) + 1).toString(),
            lastModified: formattedTime,
            name: itemData.name,
            number: itemData.number || 0,
            previousNumber: null,
        };

        const docRef = await addDoc(collection(db, "items"), newItem);
        return docRef.id;
    } catch (error) {
        console.error("Error creating item: ", error);
        throw error;
    }
};
