import React, { useState, useRef, useEffect } from "react";

const NumberPadInput = ({ value, onChange, ...props }) => {
    const [internalValue, setInternalValue] = useState(value || "");
    const [isPadVisible, setIsPadVisible] = useState(false);
    const inputRef = useRef(null);
    const padRef = useRef(null);

    // Handle number click
    const handleNumberClick = (num) => {
        const newValue = `${internalValue}${num}`;
        setInternalValue(newValue);
        if (onChange) onChange({ target: { value: newValue } });
    };

    // Handle backspace
    const handleBackspace = () => {
        const newValue = internalValue.slice(0, -1);
        setInternalValue(newValue);
        if (onChange) onChange({ target: { value: newValue } });
    };

    // Handle clear input
    const handleClear = () => {
        setInternalValue("");
        if (onChange) onChange({ target: { value: "" } });
    };

    // Show number pad when input is focused
    const handleFocus = () => {
        setIsPadVisible(true);
    };

    // Hide number pad when clicking outside
    useEffect(() => {
        const handleClickOutside = (event) => {
            if (
                padRef.current &&
                !padRef.current.contains(event.target) &&
                inputRef.current &&
                !inputRef.current.contains(event.target)
            ) {
                setIsPadVisible(false);
            }
        };

        document.addEventListener("mousedown", handleClickOutside);
        return () => document.removeEventListener("mousedown", handleClickOutside);
    }, []);

    return (
        <>
            <input
                ref={inputRef}
                type="text"
                value={internalValue}
                onFocus={handleFocus}
                readOnly
                className="number-input"
                {...props} // Spread all input props
            />
            {isPadVisible && (
                <div ref={padRef} className="number-pad fixed-pad">
                    {[1, 2, 3, 4, 5, 6, 7, 8, 9, 0].map((num) => (
                        <button key={num} type="button" className="num-btn" onClick={() => handleNumberClick(num)}>
                            {num}
                        </button>
                    ))}
                    <button type="button" className="num-btn backspace" onClick={handleBackspace}>
                        ⌫
                    </button>
                    <button type="button" className="num-btn clear" onClick={handleClear}>
                        C
                    </button>
                </div>
            )}
        </>
    );
};

export default NumberPadInput;
