import React, { useEffect } from 'react';
import './Cards.css';
import background_metromesh from '../../assets/backgrounds/background_metromesh.jpg'

const IntroCard = ({ style, drop, text }) => {
    useEffect(() => {
        if (drop) {
            document.body.style.overflow = 'hidden';
        } else {
            document.body.style.overflow = 'unset';
        }
        // Clean up function to ensure overflow style is reset when the component unmounts or `drop` is false
        return () => {
            document.body.style.overflow = 'unset';
        };
    }, [drop]); // Add `drop` to the dependency array

    if (drop) {
        return (
            <div style={style}>
                <p className='introText'>
                    {text}
                </p>
                <img className='introImage' src={background_metromesh} alt="Description" />
            </div>
        );
    }
    return null;
};

export default IntroCard;