import { db } from "../firebase";
import { collection, addDoc, getDocs } from "firebase/firestore";

/**
 * Fetches all existing supplier names from Firestore (PRIVATE function).
 * This is used internally to check for duplicate suppliers.
 */
const fetchSuppliers = async () => {
    try {
        const querySnapshot = await getDocs(collection(db, "suppliers"));
        return querySnapshot.docs.map((doc) => doc.data().name);
    } catch (error) {
        console.error("Error fetching suppliers: ", error);
        throw error;
    }
};

/**
 * Creates a new supplier **only if it does not already exist**.
 * @param {string} supplierName - The user-defined supplier name.
 * @returns {Promise<string>} The document ID of the newly created supplier.
 */
export const createSupplier = async (supplierName) => {
    if (!supplierName) {
        throw new Error("供應商名稱不可為空！");
    }

    try {
        // Fetch existing suppliers **inside this function** (not exported)
        const existingSuppliers = await fetchSuppliers();

        // Prevent duplicate suppliers (case-insensitive check)
        if (existingSuppliers.some((name) => name.toLowerCase() === supplierName.toLowerCase())) {
            throw new Error("擁有完全相同名稱之供應商已存在！");
        }

        // Create new supplier
        const docRef = await addDoc(collection(db, "suppliers"), {
            name: supplierName,
            moneyOwe: 0, // Default value
        });

        return docRef.id;
    } catch (error) {
        console.error("Error creating supplier: ", error);
        throw error;
    }
};
