import React, { useState, useEffect } from "react";
import { db } from "../../firebase";
import { collection, getDocs } from "firebase/firestore";
import { Link, useNavigate } from "react-router-dom";
import "./Shoei.css";
import NewRowPopup from "../../components/popups/NewRowPopup";
import NumberPadInput from "../../components/widgets/NumberPadInput";
import { editStock } from "../../utils/editStock";

const Shoei = () => {
    const [rows, setRows] = useState([]);
    const [items, setItems] = useState([]);
    const [categories, setCategories] = useState([]);
    const [suppliers, setSuppliers] = useState([]);
    const [selectedSupplier, setSelectedSupplier] = useState("");
    const [finalDiscountedPrice, setFinalDiscountedPrice] = useState(0);
    const [popupOpen, setPopupOpen] = useState(false);
    const navigate = useNavigate();

    useEffect(() => {
        const classNamesToHide = [".container", ".separatorStyle", ".progressBarStyle"];

        const elements = classNamesToHide.flatMap(selector => Array.from(document.querySelectorAll(selector)));
        elements.forEach(element => (element.style.display = "none"));

        return () => {
            elements.forEach(element => (element.style.display = ""));
        };
    }, []);

    const sortAlphaNumeric = (a, b) => {
        const regex = /\d+/g;
        const aText = a.replace(regex, "").trim().toLowerCase();
        const bText = b.replace(regex, "").trim().toLowerCase();
        const aNum = a.match(regex) ? parseInt(a.match(regex)[0], 10) : Infinity;
        const bNum = b.match(regex) ? parseInt(b.match(regex)[0], 10) : Infinity;

        if (aText === bText) return aNum - bNum;
        return aText.localeCompare(bText);
    };

    useEffect(() => {
        const fetchItems = async () => {
            try {
                const querySnapshot = await getDocs(collection(db, "items"));
                const fetchedItems = querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));

                const uniqueCategories = [...new Set(fetchedItems.map(item => item.category))].sort(sortAlphaNumeric);
                const sortedItems = fetchedItems.sort((a, b) => sortAlphaNumeric(a.name, b.name));

                setItems(sortedItems);
                setCategories(uniqueCategories);
            } catch (error) {
                console.error("Error fetching items:", error);
            }
        };

        const fetchSuppliers = async () => {
            try {
                const querySnapshot = await getDocs(collection(db, "suppliers"));
                const fetchedSuppliers = querySnapshot.docs.map(doc => doc.data().name);
                setSuppliers(fetchedSuppliers);
            } catch (error) {
                console.error("Error fetching suppliers:", error);
            }
        };

        fetchItems();
        fetchSuppliers();
    }, []);

    const handleAddRow = (newRow) => {
        setRows([...rows, newRow]);
    };

    const removeRow = () => {
        if (rows.length === 0) {
            window.alert("無法刪除！");
            return;
        }

        const confirmDelete = window.confirm("確定要刪除當前最後一行嗎？");
        if (confirmDelete) {
            setRows(rows.slice(0, -1));
        }
    };

    // Calculate Final Total Price
    const finalTotalPrice = rows.reduce((sum, row) => sum + row.totalPrice, 0);

    // Update finalDiscountedPrice when total price changes
    useEffect(() => {
        setFinalDiscountedPrice(finalTotalPrice);
    }, [finalTotalPrice]);

    const handlePrintAndRedirect = async () => {
        try {
            if (rows.length === 0) {
                alert("請至少新增一行再行列印！");
                return;
            }
            else if (selectedSupplier === "") {
                alert("請選擇供應商！");
                return;
            }

            for (const row of rows) {
                const targetedItem = items.find(item => item.name === row.name);
                if (targetedItem) {
                    const newStock = targetedItem.number - parseInt(row.number, 10);
                    await editStock(targetedItem.id, newStock);
                }
            }

            window.print();
            navigate("/ShoeiWarehouse");
        } catch (error) {
            console.error("Error updating stock:", error);
            alert("發生錯誤，請稍後再試。");
        }
    };

    return (
        <div style={{ padding: "20px", textAlign: "center", backgroundColor: "white" }}>
            <h1>大橋機車有限公司</h1>
            <h2>台北市延平北路三段128號</h2>
            <h3>TEL: 2591-9636, FAX: 2586-2955</h3>
            <div className="suppliersInput">
                <select
                    value={selectedSupplier}
                    onChange={(e) => setSelectedSupplier(e.target.value)}
                >
                    <option value="">選擇供應商</option>
                    {suppliers.map((supplier, i) => (
                        <option key={i} value={supplier}>{supplier}</option>
                    ))}
                </select>
            </div>
            <table className="shoetable">
                <thead>
                    <tr>
                        <th>分類</th>
                        <th>尺寸</th>
                        <th>數量</th>
                        <th>單價</th>
                        <th>總價</th>
                        <th>備註</th>
                    </tr>
                </thead>
                <tbody>
                    {rows.map((row, index) => (
                        <tr key={index}>
                            <td>{row.category}</td>
                            <td>{row.name}</td>
                            <td>{row.number}</td>
                            <td>{row.price}</td>
                            <td>{row.totalPrice}</td>
                            <td>{row.note}</td>
                        </tr>
                    ))}
                </tbody>
                <tfoot>
                    <tr>
                        <td colSpan="4">加總價格</td>
                        <td>{finalTotalPrice}</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td colSpan="4">最終價格</td>
                        <td style={{ padding: '0' }}>
                            <NumberPadInput
                                value={finalDiscountedPrice}
                                onChange={(e) => setFinalDiscountedPrice(parseInt(e.target.value, 10) || '')}
                                min="0"
                                style={{ fontSize: '28px' }}
                            />
                        </td>
                        <td></td>
                    </tr>
                </tfoot>
            </table>
            <div>
                <button onClick={() => setPopupOpen(true)} className="add-button">新增一行</button>
                <button onClick={removeRow} className="remove-button">移除一行</button>
                <button onClick={handlePrintAndRedirect} className="print-button">日結列印</button>
                {/* <button onClick={null} className="save-button">月結儲存</button> */}
            </div>
            <Link to="/FirstPage">
                <button className="nav-button">返回首頁</button>
            </Link>
            <NewRowPopup isOpen={popupOpen} onClose={() => setPopupOpen(false)}
                categories={categories} items={items} onSave={handleAddRow} />
        </div>
    );
};

export default Shoei;
