import React, { useState, useEffect } from "react";
import { db } from "../../firebase";
import { collection, getDocs } from "firebase/firestore";
import { Link } from 'react-router-dom';
import "./WarehousePage.css";

export default function WarehousePage() {
    const [items, setItems] = useState([]);
    const [filteredItems, setFilteredItems] = useState([]);
    const [categoryFilter, setCategoryFilter] = useState("");
    const [showNotCleaned, setShowNotCleaned] = useState(false);
    const [showAvailable, setShowAvailable] = useState(false);
    const [sortBy, setSortBy] = useState("categoryName");

    useEffect(() => {
        const fetchItems = async () => {
            const querySnapshot = await getDocs(collection(db, "items"));
            const fetchedItems = querySnapshot.docs.map(doc => doc.data());
            setItems(fetchedItems);
            setFilteredItems(fetchedItems);
        };
        fetchItems();
    }, []);

    // Function to sort alphabetically and numerically
    const sortAlphaNumeric = (a, b) => {
        const regex = /\d+/g; // Matches numbers
        const aText = a.replace(regex, "").trim().toLowerCase();
        const bText = b.replace(regex, "").trim().toLowerCase();
        const aNum = a.match(regex) ? parseInt(a.match(regex)[0], 10) : Infinity;
        const bNum = b.match(regex) ? parseInt(b.match(regex)[0], 10) : Infinity;

        if (aText === bText) return aNum - bNum; // Sort numerically if text is the same
        return aText.localeCompare(bText); // Otherwise, sort alphabetically
    };

    // Function to apply filters and sorting
    useEffect(() => {
        let filtered = [...items];
        filtered.sort((a, b) => {
            const categoryComparison = sortAlphaNumeric(a.category, b.category);
            return categoryComparison !== 0 ? categoryComparison : sortAlphaNumeric(a.name, b.name);
        });
        if (categoryFilter) {
            filtered = filtered.filter(item =>
                item.category.toLowerCase().includes(categoryFilter.toLowerCase()) // Case-insensitive search
            );
        }
        if (showNotCleaned) {
            filtered = filtered.filter(item => item.cleanupNumber > 0);
        }
        if (showAvailable) {
            filtered = filtered.filter(item => item.number > 0);
        }

        // Sorting logic
        if (sortBy === "lastModified") {
            filtered.sort((a, b) => b.lastModified.localeCompare(a.lastModified));
        } else if (sortBy === "lastCleanUp") {
            filtered.sort((a, b) => b.lastCleanUp.localeCompare(a.lastCleanUp));
        } else if (sortBy === "number") {
            filtered.sort((a, b) => b.number - a.number);
        }

        setFilteredItems(filtered);
    }, [categoryFilter, showNotCleaned, showAvailable, sortBy, items]);

    useEffect(() => {
        const classNamesToHide = [".container", ".separatorStyle", ".progressBarStyle"];

        // Hide elements
        const elements = classNamesToHide.flatMap(selector => Array.from(document.querySelectorAll(selector)));
        elements.forEach(element => (element.style.display = "none"));

        // Cleanup: Restore visibility when leaving the page
        return () => {
            elements.forEach(element => (element.style.display = ""));
        };
    }, []);

    return (
        <div className="warehouse-container">
            <h1>庫存管理</h1>
            <div className="filters">
                <input
                    type="text"
                    placeholder="🔍 搜尋類別"
                    value={categoryFilter}
                    onChange={(e) => setCategoryFilter(e.target.value)}
                />
                <label>
                    <input
                        type="checkbox"
                        checked={showNotCleaned}
                        onChange={() => setShowNotCleaned(!showNotCleaned)}
                        style={{ width: 'min-content' }}
                    />
                    僅顯示待出貨項目
                </label>
                <label>
                    <input
                        type="checkbox"
                        checked={showAvailable}
                        onChange={() => setShowAvailable(!showAvailable)}
                        style={{ width: 'min-content' }}
                    />
                    僅顯示有存貨項目
                </label>
                <select onChange={(e) => setSortBy(e.target.value)} value={sortBy}>
                    <option value="lastModified">按更改時間排序</option>
                    <option value="lastCleanUp">按出貨時間排序</option>
                    <option value="number">按庫存數量排序</option>
                </select>
            </div>
            <table className="warehouse-table">
                <thead>
                    <tr>
                        <th>分類</th>
                        <th>尺寸</th>
                        {/* <th>最近更新時間</th>
                        <th>最近出貨時間</th> */}
                        <th>現有庫存數</th>
                        <th>待出貨</th>
                    </tr>
                </thead>
                <tbody>
                    {filteredItems.map((item, index) => (
                        <tr key={index}>
                            <td>{item.category}</td>
                            <td>{item.name}</td>
                            {/* <td>{item.lastModified}</td>
                            <td>{item.lastCleanUp}</td> */}
                            <td className={item.number === 0 ? "low-stock" : ""}>{item.number}</td>
                            <td>{item.cleanupNumber}</td>
                        </tr>
                    ))}
                </tbody>
            </table>
            <Link to="/FirstPage">
                <button className="nav-button">返回首頁</button>
            </Link>
        </div>
    );
}
