import React from "react";
import { Navigate, Outlet } from "react-router-dom";
import { useAuth } from "./AuthContext";
// Public Route - Accessible to Everyone
export function PublicRoute() {
    return <Outlet />;
}

// Admin Route - Restricted to Admin Users
export function AdminRoute() {
    const { user, role, loading } = useAuth();
    if (loading) return <h1>Loading...</h1>;
    return user && role === "admin" ? <Outlet /> : <Navigate to="/login" />;
}

// Regular Route - Restricted to Regular Users
export function RegularRoute() {
    const { user, role, loading } = useAuth();
    if (loading) return <h1>Loading...</h1>;
    return user && (role === "regular" || role === "admin") ? <Outlet /> : <Navigate to="/login" />;
}