import React, { useState, useEffect } from "react";
import "./Popup.css";
import { createItem } from "../../utils/createItem";
import { createName, fetchCategories } from "../../utils/createName";
import { createSupplier } from "../../utils/createSupplier";
import { editStock, fetchItems } from "../../utils/editStock";
import NumberPadInput from "../widgets/NumberPadInput";

const Popup = ({ isOpen, onClose, mode = "createItem" }) => {
    const [category, setCategory] = useState("");
    const [name, setName] = useState("");
    const [number, setNumber] = useState();
    const [supplierName, setSupplierName] = useState("");
    const [categories, setCategories] = useState([]);
    const [items, setItems] = useState([]);
    const [selectedItem, setSelectedItem] = useState(null);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState("");

    // Fetch categories on mode change
    useEffect(() => {
        if (mode === "createName" || mode === "editStock") {
            fetchCategories()
                .then(setCategories)
                .catch((error) => console.error("Failed to fetch categories", error));
        }
    }, [mode]);

    // Fetch items when a category is selected (for editStock)
    useEffect(() => {
        if (mode === "editStock" && category) {
            fetchItems(category)
                .then(setItems)
                .catch((error) => console.error("Failed to fetch items", error));
        }
    }, [category, mode]);

    // Handle name change and set stock automatically
    const handleNameChange = (selectedName) => {
        setName(selectedName);
        const matchedItem = items.find(item => item.name === selectedName && item.category === category);
        if (matchedItem) {
            setSelectedItem(matchedItem);
        } else {
            setSelectedItem(null);
        }
        console.log(matchedItem);
    };

    // Reset fields when category changes
    const handleCategoryChange = (newCategory) => {
        setCategory(newCategory);
        setName("");        // Reset name
        setNumber();      // Reset number
        setSelectedItem(null); // Reset selected item
        setItems([]);       // Clear item list to prevent stale data
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        setError("");
        if (mode === "createItem" && (!category.trim() || !name.trim() || number < 0)) {
            alert("請填寫所有資料！");
            return;
        }
        if (mode === "createName" && (!category || !name.trim() || number < 0)) {
            alert("請填寫所有資料！");
            return;
        }
        if (mode === "createSupplier" && !supplierName.trim()) {
            alert("請輸入供應商名稱！");
            return;
        }
        if (mode === "editStock") {
            if (!category || !selectedItem || !number || number < 0) {
                alert("請提供完整資料！");
                return;
            }
        }
        setLoading(true);
        try {
            if (mode === "createItem") {
                await createItem({ category, name, number: parseInt(number, 10) });

            } else if (mode === "createName") {
                await createName({ category, name });

            } else if (mode === "createSupplier") {
                await createSupplier(supplierName);

            } else if (mode === "editStock") {
                await editStock(selectedItem.id, parseInt(number, 10));
            }

            onClose(); // Close popup after success
        } catch (err) {
            alert(`發生錯誤：${err.message}`);
        } finally {
            setLoading(false);
        }
    };

    if (!isOpen) return null;

    return (
        <div className="popup-overlay">
            <div className="popup-content">
                <h2>
                    {mode === "createItem" ? "新增產品" :
                        mode === "createName" ? "新增尺寸" :
                            mode === "createSupplier" ? "新增供應商" :
                                "編輯庫存"}
                </h2>
                {error && <p className="error-message">{error}</p>}
                <form onSubmit={handleSubmit}>
                    {/* CreateItem - Manual Category Input */}
                    {mode === "createItem" && (
                        <input
                            type="text"
                            placeholder="輸入類別"
                            value={category}
                            onChange={(e) => setCategory(e.target.value)}
                        />
                    )}

                    {/* CreateName & EditStock - Category Dropdown */}
                    {(mode === "createName" || mode === "editStock") && (
                        <select value={category} style={{ marginTop: '3vw', marginBottom: '1vw', width: '60%' }} onChange={(e) => handleCategoryChange(e.target.value)}>
                            <option value="" disabled>選擇類別</option>
                            {categories.length > 0 ? (
                                categories.map((cat) => <option key={cat} value={cat}>{cat}</option>)
                            ) : (
                                <option value="" disabled>無類別資料，請檢查網路連線</option>
                            )}
                        </select>
                    )}

                    {/* EditStock - Item Selection */}
                    {mode === "editStock" && (
                        <select value={name} style={{ marginTop: '3vw', marginBottom: '1vw', width: '60%' }} onChange={(e) => handleNameChange(e.target.value)}>
                            <option value="" disabled>選擇尺寸</option>
                            {items.length > 0 ? (
                                items.map((item) => <option key={item.id} value={item.name}>{item.name}</option>)
                            ) : (
                                <option value="" disabled>無尺寸資料，請新增尺寸</option>
                            )}
                        </select>
                    )}

                    {/* Name Input (CreateItem, CreateName) */}
                    {(mode === "createItem" || mode === "createName") && (
                        <input
                            type="text"
                            placeholder="輸入名稱"
                            value={name}
                            onChange={(e) => setName(e.target.value)}
                        />
                    )}

                    {/* Stock Input (CreateItem, CreateName, EditStock) */}
                    {(mode === "createItem" || mode === "createName" || mode === "editStock") && (
                        <NumberPadInput
                            value={number}
                            placeholder="輸入庫存"
                            onChange={(e) => setNumber(e.target.value)}
                            min="0"
                        />
                    )}

                    {/* Supplier Input */}
                    {mode === "createSupplier" && (
                        <input
                            type="text"
                            placeholder="輸入供應商"
                            value={supplierName}
                            onChange={(e) => setSupplierName(e.target.value)}
                        />
                    )}

                    <button type="submit" disabled={loading} className="save-button">
                        {loading ? "儲存中..." : "儲存"}
                    </button>
                    <button type="button" onClick={onClose} className="cancel-button">
                        取消
                    </button>
                </form>
            </div>
        </div >
    );
};

export default Popup;