import React, { useState } from "react";
import "./NewRowPopup.css";
import NumberPadInput from "../widgets/NumberPadInput";

const NewRowPopup = ({ isOpen, onClose, categories, items, onSave }) => {
    const [formData, setFormData] = useState({
        category: "",
        name: "",
        number: "",
        price: "",
        note: "",
    });

    const [maxStock, setMaxStock] = useState(null); // Store the available stock for the selected item

    // Handle category selection
    const handleCategoryChange = (value) => {
        setFormData({
            category: value,
            name: "", // Reset name when category changes
            number: "", // Reset number when category changes
            price: formData.price,
            note: formData.note,
        });
        setMaxStock(null); // Reset stock since name selection is reset
    };

    // Handle name selection
    const handleNameChange = (value) => {
        const selectedItem = items.find(item => item.name === value);
        setFormData(prev => ({
            ...prev,
            name: value,
            number: "", // Reset number when a new name is selected
        }));
        console.log(selectedItem);
        setMaxStock(selectedItem ? selectedItem.number : null); // Update available stock
    };

    // Handle number input with stock validation
    const handleNumberChange = (value) => {
        const numValue = parseInt(value, 10) || "";
        setFormData(prev => ({ ...prev, number: numValue }));
        if (maxStock !== null && numValue > maxStock) {
            alert(`警告：輸入數量超過庫存 (${maxStock})！`);
            return;
        }
    };

    // Handle form submission
    const handleSave = () => {
        if (!formData.category || !formData.name || formData.number <= 0 || formData.price < 0) {
            alert("請確保所有欄位都已填寫且數量必須大於零！");
            return;
        }

        if (maxStock !== null && formData.number > maxStock) {
            alert(`無法儲存，數量超過庫存 (${maxStock})！`);
            return;
        }

        onSave({
            ...formData,
            number: parseInt(formData.number, 10),
            price: parseInt(formData.price, 10),
            totalPrice: parseInt(formData.number, 10) * parseInt(formData.price, 10),
        });

        onClose();
        setFormData({ category: "", name: "", number: "", price: "", note: "" });
        setMaxStock(null); // Reset stock
    };

    if (!isOpen) return null;

    return (
        <div className="popup-overlay">
            <div className="popup-content" style={{ width: '50vw' }}>
                <h2>新增一行</h2>
                <div className="popup-field">
                    <label>分類</label>
                    <select value={formData.category} onChange={(e) => handleCategoryChange(e.target.value)}>
                        <option value="">選擇類別</option>
                        {categories.map((cat, i) => (
                            <option key={i} value={cat}>{cat}</option>
                        ))}
                    </select>
                </div>
                <div className="popup-field">
                    <label>尺寸</label>
                    <select
                        value={formData.name}
                        onChange={(e) => handleNameChange(e.target.value)}
                        disabled={!formData.category}
                    >
                        <option value="">選擇尺寸</option>
                        {items.filter(item => item.category === formData.category).map((item, i) => (
                            <option key={i} value={item.name}>{item.name}</option>
                        ))}
                    </select>
                </div>
                <div className="popup-field">
                    <label>數量 (最大庫存: {maxStock ?? "N/A"})</label>
                    <NumberPadInput
                        value={formData.number}
                        min="1"
                        onChange={(e) => handleNumberChange(e.target.value)}
                        disabled={!formData.name}
                    />
                </div>
                <div className="popup-field">
                    <label>單價</label>
                    <NumberPadInput
                        value={formData.price}
                        min="1"
                        onChange={(e) => setFormData(prev => ({ ...prev, price: e.target.value }))}
                    />
                </div>
                <div className="popup-field">
                    <label>備註</label>
                    <input
                        type="text"
                        value={formData.note}
                        onChange={(e) => setFormData(prev => ({ ...prev, note: e.target.value }))}
                    />
                </div>
                <div className="popup-buttons">
                    <button className="save-button" onClick={handleSave} style={{ margin: '0' }}>儲存</button>
                    <button className="cancel-button" onClick={onClose} style={{ margin: '0' }}>取消</button>
                </div>
            </div>
        </div>
    );
};

export default NewRowPopup;
