import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom';
import "./FirstPage.css";
import { useAuth } from '../../AuthContext';
import { useNavigate } from 'react-router-dom';
import { signOut } from "firebase/auth";
import { auth } from "../../firebase";
import Popup from '../../components/popups/Popup';

export default function FirstPage() {
    const { user, role } = useAuth();
    const navigate = useNavigate();
    const [popupMode, setPopupMode] = useState(null);

    const handleLogout = async () => {
        try {
            navigate("/login");
            await signOut(auth);
        } catch (error) {
            console.error("Logout failed:", error.message);
        }
    };

    useEffect(() => {
        const classNamesToHide = [".container", ".separatorStyle", ".progressBarStyle"];

        // Hide elements
        const elements = classNamesToHide.flatMap(selector => Array.from(document.querySelectorAll(selector)));
        elements.forEach(element => (element.style.display = "none"));

        // Cleanup: Restore visibility when leaving the page
        return () => {
            elements.forEach(element => (element.style.display = ""));
        };
    }, []);

    return (
        <div className='firstContainer'>
            <Link to="/ShoeiWarehouse">
                <button className="home-button">庫存管理</button>
            </Link>
            {role === 'admin' && <Link to="/ShoeiDashBoard">
                <button className="home-button">出貨訂單</button>
            </Link>}
            {role === 'admin' && <Link to="/ShoeiDashBoard">
                <button className="home-button">月結計算</button>
            </Link>}
            {user && (
                <button onClick={handleLogout} className="logout-button" >
                    立即登出
                </button>
            )}
            {role === 'admin' && <div className="home-button">
                <button onClick={() => setPopupMode("createItem")} className='creation'>新增產品</button>
                <button onClick={() => setPopupMode("createName")} className='creation'>新增尺寸</button>
                <button onClick={() => setPopupMode("createSupplier")} className='creation'>新增供應商</button>
                <button onClick={() => setPopupMode("editStock")} className='deletion'>更改庫存</button>
            </div>}
            <Popup
                isOpen={!!popupMode}
                onClose={() => setPopupMode(null)}
                mode={popupMode}
            />
        </div>
    )
}
