import React, { useState, useEffect } from 'react';
import { HashRouter as Router, Routes, Route } from "react-router-dom";
// import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import './App.css';
import Home from './pages/HomePage/Home';
import Header from './components/Header/Header';
import ProgressBar from './components/widgets/ProgressBar';
import background_home from './assets/backgrounds/background_home.png';
import Shoei from './pages/ShoeiPage/Shoei';
import WarehousePage from './pages/WarehousePage/WarehousePage';
import { AuthProvider } from './AuthContext';
import { PublicRoute, AdminRoute, RegularRoute } from './ProtectedRoute';
import LoginPage from './pages/LoginPage/LoginPage';
import EditWarehousePage from './pages/EditWarehousePage/EditWarehousePage';
import { useAutoLogout } from './useAutoLogout';
import FirstPage from './pages/FirstPage/FirstPage';

function App() {
  useAutoLogout();
  const [scrollPosition, setScrollPosition] = useState(0);
  const [isBackgroundFixed, setIsBackgroundFixed] = useState(false);

  useEffect(() => {
    const imageRatio = 0.57497;
    const handleScroll = () => {
      const position = window.pageYOffset;
      const windowHeight = window.innerHeight;
      const windowWidth = window.innerWidth;
      const imageHeight = windowWidth * imageRatio;

      if (position + windowHeight >= imageHeight) {
        setIsBackgroundFixed(true);
      } else {
        setIsBackgroundFixed(false);
      }

      const scrollPercentage = (position / (document.documentElement.scrollHeight - windowHeight)) * 100;
      setScrollPosition(scrollPercentage);
    };

    window.addEventListener('scroll', handleScroll, { passive: true });

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  const getBackgroundColor = () => {
    if (scrollPosition < 10 || scrollPosition > 85) return 'transparent';
    if (scrollPosition < 20) return `rgba(0, 0, 0, ${(scrollPosition - 10) / 10})`;
    if (scrollPosition > 75) return `rgba(0, 0, 0, ${(85 - scrollPosition) / 10})`;
    return 'rgba(0, 0, 0, 1)';
  };

  const blackBackgroundStyle = {
    backgroundColor: getBackgroundColor(),
    transition: 'background-color 0.3s',
  };

  const appStyle = {
    textAlign: 'center',
    fontFamily: "'Arimo', sans-serif",
    backgroundImage: `url(${background_home})`,
    backgroundSize: isBackgroundFixed ? 'cover' : 'contain',
    backgroundAttachment: isBackgroundFixed ? 'fixed' : 'scroll',
    backgroundPosition: isBackgroundFixed ? 'bottom' : 'top',
    paddingTop: '60px',
    position: 'relative',
  };

  return (
    <AuthProvider>
      <Router>
        <div style={appStyle}>
          <div style={blackBackgroundStyle}>
            <Header />
            <ProgressBar />
            <Routes>
              {/* Public Routes (Accessible by Everyone) */}
              <Route element={<PublicRoute />}>
                <Route path="/" element={<Home />} />
                <Route path="/login" element={<LoginPage />} />
              </Route>

              <Route element={<RegularRoute />}>
                <Route path="/FirstPage" element={<FirstPage />} />
                <Route path="/ShoeiWarehouse" element={<WarehousePage />} />
              </Route>

              {/* Admin Routes (Only for Admins) */}
              <Route element={<AdminRoute />}>
                <Route path="/ShoeiDashBoard" element={<Shoei />} />
                <Route path="/ShoeiEdit" element={<EditWarehousePage />} />
              </Route>

              {/* Redirect Unauthorized Users to Login */}
              <Route path="*" element={<LoginPage />} />
            </Routes>
          </div>
        </div>
      </Router>
    </AuthProvider>
  );
}

export default App;
