import { db } from "../firebase";
import { collection, addDoc, getDocs } from "firebase/firestore";

/**
 * Fetches all unique categories from the "items" collection in Firestore.
 * @returns {Promise<string[]>} Array of unique category names.
 */
export const fetchCategories = async () => {
    try {
        const querySnapshot = await getDocs(collection(db, "items"));
        const categories = [
            ...new Set(querySnapshot.docs.map((doc) => doc.data().category)),
        ];
        return categories.sort((a, b) => a.localeCompare(b)); // Alphanumeric sorting
    } catch (error) {
        console.error("Error fetching categories: ", error);
        throw error;
    }
};

/**
 * Creates a new item by selecting an existing category and providing a new name.
 * @param {Object} itemData - The item details from user input.
 * @returns {Promise<string>} The document ID of the newly created item.
 */
export const createName = async (itemData) => {
    try {
        const now = new Date();
        const formattedTime = now.toISOString().replace(/[-T:.Z]/g, "").slice(0, 14); // YYYYMMDDHHMMSS

        const newItem = {
            category: itemData.category,
            cleanupNumber: 0,
            lastCleanUp: (parseInt(formattedTime) + 1).toString(),
            lastModified: formattedTime,
            name: itemData.name,
            number: 1,
            previousNumber: null,
        };

        const docRef = await addDoc(collection(db, "items"), newItem);
        return docRef.id;
    } catch (error) {
        console.error("Error creating item: ", error);
        throw error;
    }
};
