import { db } from "../firebase";
import { collection, getDocs, doc, updateDoc } from "firebase/firestore";

/**
 * Fetches items filtered by category.
 * @param {string} category - The selected category.
 * @returns {Promise<Array>} List of items within the given category.
 */
export const fetchItems = async (category) => {
    try {
        const querySnapshot = await getDocs(collection(db, "items"));
        const fetchedItems = querySnapshot.docs
            .map((doc) => ({ id: doc.id, ...doc.data() }))
            .filter((item) => item.category === category);

        return fetchedItems.sort((a, b) => a.name.localeCompare(b.name)); // Sort by name
    } catch (error) {
        console.error("Error fetching items: ", error);
        throw error;
    }
};

/**
 * Updates the stock of an existing item.
 * @param {string} itemId - The Firestore document ID of the item.
 * @param {number} newStock - The new stock value.
 * @returns {Promise<void>}
 */
export const editStock = async (itemId, newStock) => {
    try {
        const itemRef = doc(db, "items", itemId);
        await updateDoc(itemRef, { number: newStock });
    } catch (error) {
        throw error;
    }
};