import React, { useState, useEffect } from "react";
import { signInWithEmailAndPassword } from "firebase/auth";
import { auth } from "../../firebase";
import { useNavigate } from "react-router-dom";
import "./LoginPage.css";

export default function LoginPage() {
    const [password, setPassword] = useState(""); // Only ask for password
    const [error, setError] = useState("");
    const navigate = useNavigate();

    const userCredentials = {
        "0425875597": "aunt@warehouse.com",
        "0225919636": "shoei@warehouse.com"
    };

    const handleLogin = async (e) => {
        e.preventDefault();
        setError(""); // Reset error message

        if (userCredentials[password]) {
            const email = userCredentials[password];

            try {
                await signInWithEmailAndPassword(auth, email, password);
                setTimeout(() => {
                    navigate("/FirstPage"); // Navigate after 1 second
                }, 1000);
            } catch (error) {
                setError("登入失敗，請重試");
            }
        } else {
            setError("密碼錯誤，請重新輸入");
        }
    };

    // Handle number pad input
    const handleNumberClick = (num) => {
        if (password.length < 10) { // Limit password length to prevent extra input
            setPassword(password + num);
        }
    };

    // Handle backspace
    const handleBackspace = () => {
        setPassword(password.slice(0, -1));
    };

    // Handle clear
    const handleClear = () => {
        setPassword("");
    };

    useEffect(() => {
        const classNamesToHide = [".container", ".separatorStyle", ".progressBarStyle"];
        const elements = classNamesToHide.flatMap(selector => Array.from(document.querySelectorAll(selector)));
        elements.forEach(element => (element.style.display = "none"));

        return () => {
            elements.forEach(element => (element.style.display = ""));
        };
    }, []);

    return (
        <div className="login-container">
            <div className="login-box">
                <h2>請輸入密碼</h2>
                <p className="subtext">使用數字密碼登入，登入時間最多需要十秒，請耐心等待勿反覆按壓嘗試</p>

                {error && <p className="error-message">{error}</p>}

                <form onSubmit={handleLogin}>
                    <div className="input-group">
                        <input
                            type="password"
                            inputMode="numeric"
                            pattern="[0-9]*"
                            value={password}
                            onChange={(e) => setPassword(e.target.value.replace(/\D/g, ""))}
                            placeholder="輸入密碼"
                            required
                            readOnly // Prevent manual typing; only number pad input
                        />
                    </div>

                    <div className="number-pad">
                        {[1, 2, 3, 4, 5, 6, 7, 8, 9, 0].map((num) => (
                            <button key={num} type="button" className="num-btn" onClick={() => handleNumberClick(num)}>
                                {num}
                            </button>
                        ))}
                        <button type="button" className="num-btn backspace" onClick={handleBackspace}>⌫</button>
                        <button type="button" className="num-btn clear" onClick={handleClear}>C</button>
                    </div>

                    <button type="submit" className="login-button">登入</button>
                </form>
            </div>
        </div>
    );
}
